*{
    box-sizing: border-box;
}
.button{
    z-index: 1;
   position:fixed ;
   border-radius: 50%;
   right: 30px;
   bottom: 50px;
   box-shadow: 1px 1px 5px 5px rgb(39, 38, 38);
}
.card{
    width: 18rem;
    margin: 1%;
}
.fix{
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    background-color: black;
    margin: 0%;
}
.m{
    background-color: black;
    width: 98.5vw;
}
.n{
    width: 98.5vw;
}
#myBtn{
    /* background-color: rgb(12, 85, 149); */
    bottom: 120px;
}
.int{
    position: fixed;
    z-index: 2;
   display: flex;
   background-color: rgb(47, 47, 47);
    justify-content: space-between;
    border-top-left-radius: 20px;
    border-bottom-left-radius: 20px;
    right: 0px;
    height: 40px;
    color: aliceblue;
    padding-left: 10px;
    border-bottom: 2px solid rgb(0, 0, 0);
    top: 80px;
}
input{
   box-shadow: inset 0px 0px 5px 0px black;
   background-color: rgb(47, 47, 47);
    margin: 0px;
    padding: 0px;
    width: 180px;
    border: none;
    color: aliceblue;
    padding-left: 10px;
    outline: none;
    box-sizing: border-box;
    display: none;
   
}
.tullu{
    padding: 5px;
    width: 80px;
    cursor: pointer;
}
#t2{
    padding: 5px;
    display: none;
    cursor: pointer;
}

.nothing{
    position: fixed;
    z-index: 1;
    width: 100vw;
    height: 50vh;
   text-align: center;
    font-weight: 700;
    font-size: 40px;
    color: rgb(75, 76, 78);
    background-color: black;
    padding: 15rem 1rem 30rem 1rem ;
}